@charset "UTF-8";

/*
* FLOCSSベース 独自拡張
* https://github.com/hiloki/flocss
* core mixin/function/variable
* fundation normalize/reset/font/iconfont/
* layout header/footer/wrapper/main
* object component/project/utility
* page
* vendor
*/

// ---------------------------------------------------------------
// core - variable
// ---------------------------------------------------------------
@import "core/variables";

// ---------------------------------------------------------------
// core - function
// ---------------------------------------------------------------


// ---------------------------------------------------------------
// core - mixin
// ---------------------------------------------------------------
@import "core/mixins/_arrow.scss";
@import "core/mixins/_button-3d-color.scss";
@import "core/mixins/_button-border-color.scss";
@import "core/mixins/_button-flat-color.scss";
@import "core/mixins/_button-ghost-color.scss";
@import "core/mixins/_button-size.scss";
@import "core/mixins/_circle.scss";
@import "core/mixins/_clearfix.scss";
@import "core/mixins/_font-line.scss";
@import "core/mixins/_grid.scss";
@import "core/mixins/_line-clamp.scss";
@import "core/mixins/_link-color.scss";
@import "core/mixins/_media.scss";
@import "core/mixins/_middle-line.scss";
@import "core/mixins/_opacity.scss";
@import "core/mixins/_position.scss";
@import "core/mixins/_rounded.scss";

.page-reform {
  .attention {
    width: 660px;
    margin: 0 auto 30px;
    font-weight: 300;
    @include font-line(16, 24);
  }
  .bottomBlock {
    width: 660px;
    margin: 0 auto 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .btn {
      width: 320px;
      margin: 0 5px 5px;
    }
  }
}
